<template>
    <div id="player-table">
        <div v-for="player in players" v-bind:key="player.id" class="row justify-between">
            <div class="player-name">{{ player.name }}</div>
            <font-awesome-icon icon="times" class="cursor-pointer" v-on:click="deletePlayer(player.id)" />
        </div>
        <div class="row">
            <form id="player-input-form" method="POST" action="#" v-on:submit.prevent="addPlayer">
                <input type="text" name="player-name" required="requierd" placeholder="Name" class="mr-5 p-1 rounded" v-model="playerName" />
                <input type="submit" value="Add" class="py-2 px-8 cursor-pointer rounded bg-yellow-300 text-white font-bold text-center " />
            </form>
        </div>
        <div class="row warning">
            <div v-if="warning !== null">Warning: {{ warning }}</div>
            <div v-else>&nbsp;</div>
        </div>
        <div class="row">
            <div class="mt-10 bg-yellow-300 px-8 py-2 font-bold text-center cursor-pointer flex rounded select-none" v-on:click="onClickStart">Start</div>
        </div>
    </div>
</template>

<script>
    import Trace from "../utils/Trace"
    import IdGenerator from "../utils/IdGenerator"

    const methods = {
        addPlayer() {
            if (this.playerName === "") {
                this.warning = `Player name cannot be empty.`
                return
            }

            if (this.players.some((player) => player.name === this.playerName)) {
                this.warning = `Player with name '${this.playerName}' already exists.`
                return
            }

            const newPlayer = {
                name: this.playerName,
                id: idGenerator.next()
            }

            this.players.push(newPlayer)
            this.playerName = ""
            this.warning = null
        },
        onClickStart() {
            if (this.players.length <= 1) {
                this.warning = `Add at least 2 player to the list.`
                return
            }

            this.$emit("finished", this.players)
            this.warning = null
        },
        deletePlayer(id) {
            const playerIndex = this.players.findIndex((player) => player.id === id)
            if (playerIndex < 0) return

            this.players.splice(playerIndex, 1)
        }
    }

    const idGenerator = new IdGenerator()

    export default {
        name: "PlayerSetup",
        methods,
        data: () => {
            return {
                playerName: "",
                players: [],
                warning: null
            }
        }
    }
</script>

<style scoped>
    .row {
        display: flex;
        flex-direction: row;
        margin: 10px;

        align-items: center;
    }

    .warning {
        font-size: 0.7em;
        color: red;
    }
</style>
