<template>
    <div>
        <slot></slot>
    </div>
</template>

<script>
    export default {
        props: {
            targetLineCount: {
                default: 1,
                type: Number
            },
            min: {
                default: 6,
                type: Number
            },
            max: {
                default: 24,
                type: Number
            }
        },
        data() {
            return {
                observer: null
            }
        },
        methods: {
            calculate() {
                let element = this.$el

                // then keep trying untill it fits
                let fontSize = this.max
                let stepSize = 1

                element.style.fontSize = fontSize + "px"

                while ((element.scrollHeight > element.offsetHeight || element.scrollWidth > element.offsetWidth) && fontSize > this.min) {
                    fontSize -= stepSize
                    element.style.fontSize = fontSize + "px"
                }
                // found it!!
            }
        },
        mounted() {
            this.calculate()

            if ("MutationObserver" in window && this.observer === null) {
                // Create the observer (and what to do on changes...)
                this.observer = new MutationObserver(this.calculate)

                // Setup the observer
                this.observer.observe(this.$el, { subtree: true, characterData: true })
            }
        },
        beforeUnmount: function() {
            // Clean up
            this.observer.disconnect()
        }
    }
</script>

<style scoped>
    div {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
</style>
