/**
 * @typedef {Number|String} LogLevel
 */

export class TraceObject {
    /**
     * @callback WriteLine
     * @param {LogLevel} level
     * @param {String} text
     * @param {...Object} args
     * @returns {void}
     */

    /**
     * @type {WriteLine}
     */
    #writeLine

    /**
     * @param {WriteLine} writeLineFn
     * @returns {void}
     */
    constructor(writeLineFn) {
        this.#writeLine = writeLineFn
    }

    /**
     * @returns {WriteLine}
     */
    get writeLine() {
        return this.#writeLine
    }
}

class LogLevel {
    #value

    constructor(value) {
        this.#value = value
    }

    get value() {
        return this.#value
    }
}

export const DEBUG = new LogLevel(1)
export const INFO = new LogLevel(2)
export const WARN = new LogLevel(3)
export const ERROR = new LogLevel(4)

class Trace {
    /**
     * @type {Array<TraceObject>}
     */
    static #tracers = []

    /** @constant @return {LogLevel} */
    static get DEBUG() {
        return DEBUG
    }
    /** @return {LogLevel} */
    static get INFO() {
        return INFO
    }
    /** @return {LogLevel} */
    static get WARN() {
        return WARN
    }
    /** @return {LogLevel} */
    static get ERROR() {
        return ERROR
    }

    /**
     * @type {LogLevel}
     */
    static #logLevel = Trace.DEBUG

    /**
     * @param {LogLevel} level
     * @returns {void}
     */
    static setLogLevel(level) {
        Trace.#logLevel = level
    }

    /**
     * @returns {LogLevel}
     */
    static getLogLevel() {
        return Trace.#logLevel
    }

    constructor() {
        throw new Error("Construction of static class")
    }

    /**
     * @param {TraceObject} traceObject
     * @returns {void}
     */
    static registerTraceObject(traceObject) {
        Trace.#tracers.push(traceObject)
        Trace.logDebug("Initialize new traceObject", traceObject)
    }

    /**
     * Trae Write Line
     * @param {LogLevel} level
     * @param {String} text
     * @returns {void}
     */
    static writeLine(level, text, ...args) {
        for (let i = 0; i < Trace.#tracers.length; i++) {
            Trace.#tracers[i].writeLine(level, text, ...args)
        }
    }

    /**
     * @param {LogLevel} level
     * @param {String} text
     * @param {...Object} args
     * @returns {void}
     */
    static log(level, text, ...args) {
        if (level.value < Trace.#logLevel.value) {
            return // skip
        }

        Trace.writeLine(level, text, ...args)
    }

    /**
     * @param {String} text
     * @param {...Object} args
     * @returns {void}
     */
    static logDebug(text, ...args) {
        Trace.log(Trace.DEBUG, text, ...args)
    }

    /**
     * @param {String} text
     * @param {...Object} args
     * @returns {void}
     */
    static logInfo(text, ...args) {
        Trace.log(Trace.INFO, text, ...args)
    }

    /**
     * @param {String} text
     * @param {...Object} args
     * @returns {void}
     */
    static logWarn(text, ...args) {
        Trace.log(Trace.WARN, text, ...args)
    }

    /**
     * @param {String} text
     * @param {...Object} args
     * @returns {void}
     */
    static logError(text, ...args) {
        Trace.log(Trace.ERROR, text, ...args)
    }
}

export const ConsoleTraceObject = new TraceObject((level, text, ...args) => {
    /**
     * @callback ConsoleOutput
     * @param {...Object} data
     * @returns {void}
     */

    /**
     * @callback GetConsoleOutput
     * @param {LogLevel} level
     * @returns {ConsoleOutput}
     */

    /** @type {GetConsoleOutput} */
    const getConsoleOutput = (level) => {
        switch (level) {
            case Trace.DEBUG:
                return console.debug
            case Trace.INFO:
                return console.info
            case Trace.WARN:
                return console.warn

            default:
                return console.error
        }
    }

    const consoleOutput = getConsoleOutput(level)
    consoleOutput(text, ...args)
})

export default Trace
