import APIResponse from "../Models/APIResponse"
import APIErrorResponse from "../Models/APIErrorResponse"
import APIParseErrorResponse from "../Models/APIParseErrorResponse"
import APIFetchErrorResponse from "../Models/APIFetchErrorResponse"
import Trace from "../../../utils/Trace"

class APIResponseFactory {
    /**
     * @param {Response} response
     * @returns {Promise<String>}
     */
    async #tryGetText(response) {
        try {
            return await response.text()
        } catch {
            return ""
        }
    }

    /**
     * @param {Response} response
     * @returns {Promise<APIResponse>}
     */
    async createFromResponse(response) {
        const statusCode = response.status
        const statusText = response.statusText

        console.log(response.headers)

        if (!response.ok) {
            const content = await this.#tryGetText(response)
            return new APIErrorResponse(content, statusCode, statusText)
        }

        try {
            const content = await response.json()
            return new APIResponse(content, statusCode, statusText)
        } catch (error) {
            return APIParseErrorResponse(error, 0, statusText)
        }
    }

    /**
     * @param {String} requestUrl
     * @param {Object} [requestObject]
     * @returns {Promise<APIResponse>}
     */
    async createFromRequest(requestUrl, requestObject) {
        try {
            // requestObject.credentials = "same-origin"
            const response = await fetch(requestUrl, requestObject)
            return await this.createFromResponse(response)
        } catch (error) {
            return new APIFetchErrorResponse(error, 0, null)
        }
    }
}

const apiResponseFactory = new APIResponseFactory()
export default apiResponseFactory
