<template>
    <PlayerSetup @finished="finishPlayerSetup" v-if="state === 1" />
    <SortingGame @finished="finishGame" v-if="currentVoteList !== null && state === 2" :voteList="currentVoteList" :player="currentPlayer" />
    <div v-if="state === 3" id="end-overview" ref="listContainer">
        <div v-for="voteItem in globalVoteList" v-bind:key="voteItem.meal.recipe.id" class="meal-container">
            <meal-card :meal="voteItem.meal" />
        </div>
    </div>
</template>

<script>
    import subscriptionAPI from "../API/subscription/SubscriptionAPI"
    import productAPI from "../API/products/ProductsAPI"
    import Trace from "../utils/Trace"
    import PlayerSetup from "./PlayerSetup.vue"
    import SortingGame from "./SortingGame.vue"
    import MealCard from "./MealCard.vue"

    /**
     * @param {Array<Meal>} meal
     * @param {String} preset
     *
     * @returns {Array<Meal>}
     */
    function getValidMeals(meals, preset) {
        const isEmptyOrSpaces = (str) => str === null || str.match(/^\s*$/) !== null
        const hasPreset = !isEmptyOrSpaces(preset)

        /**
         * @param {Meal} meal
         * @returns {Boolean}
         */
        const isValidMeal = (meal) => {
            if (!hasPreset) {
                return true
            }

            if (meal.recipe.tags === undefined) {
                return false
            }

            return meal.recipe.tags.some((tag) => {
                return tag.type.toLowerCase() === preset
            })
        }

        return meals.filter((meal) => isValidMeal(meal))
    }

    function createVoteList(mealList) {
        return mealList.map((meal) => {
            return { meal, score: 0 }
        })
    }

    export default {
        name: "Home",
        components: { PlayerSetup, SortingGame, MealCard },
        async created() {
            const info = await subscriptionAPI.requestOverview()
            const products = await productAPI.requestItems(info.id, info.productType.handle, new Date())

            const preset = info.preset
            this.meals = getValidMeals(products.meals, preset)

            // this.finishPlayerSetup([
            //     { name: "Hans", id: 1 },
            //     { name: "Suzanne", id: 2 }
            // ])
        },

        methods: {
            finishPlayerSetup(players) {
                this.players = players
                this.state++

                this.globalVoteList = createVoteList(this.meals)

                this.setupGame(this.players[(this.currentPlayerIndex = 0)])
            },
            setupGame(player) {
                this.currentPlayer = player
                this.currentVoteList = createVoteList(this.meals)
            },
            finishGame(voteList) {
                voteList.forEach((voteItem) => {
                    const globalItem = this.globalVoteList.find((i) => i.meal === voteItem.meal)
                    globalItem.score += voteItem.score
                })

                this.currentPlayerIndex++
                if (this.currentPlayerIndex === this.players.length) {
                    this.globalVoteList = this.globalVoteList
                        .sort((a, b) => {
                            const valueA = a.score
                            const valueB = b.score

                            if (valueA > valueB) return -1
                            if (valueA < valueB) return 1
                            return 0.5 - Math.random()
                        })
                        .slice(0, 4)

                    this.state++
                    this.$refs.listContainer.scrollTop = 0
                } else {
                    this.setupGame(this.players[this.currentPlayerIndex])
                }
            }
        },

        data: () => {
            return {
                receivedMeals: false,
                state: 1,
                players: [],
                currentPlayerIndex: 0,
                meals: [],
                currentVoteList: null
            }
        }
    }
</script>

<style scoped>
    #end-overview {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100%;

        overflow-y: scroll;
    }

    .meal-container {
        height: 70%;
        width: 95%;
        flex-shrink: 0;
        flex-grow: 0;
        margin-bottom: 20px;
        position: relative;
        margin-left: auto;
        margin-right: auto;
    }
</style>
