import helloFreshAPI, { APIPaths } from "../HelloFreshAPI"
import subscriptionRequestFactory from "./Factories/SubscriptionRequestFactory"

class SubscriptionAPI {
    /**
     * @returns { Promise<import("./Models/SubscriptionOverviewResponseModel").SubscriptionOverviewModel> }
     */
    async requestOverview() {
        const request = subscriptionRequestFactory.createOverviewRequest()
        const response = await helloFreshAPI.sendRequest(APIPaths.SUBSCRIPTION_URL, { for: "my-deliveries", country: "nl", locale: "nl-NL" }, { method: "GET", body: request })

        if (response.hasError()) {
            return false
        }

        /** @type { SubscriptionOverviewResponseModel }*/
        const responseModel = response.getContent()
        return responseModel.items.first()
    }
}

export const subscriptionAPI = new SubscriptionAPI()
export default subscriptionAPI
