import "./extensions/extensions"
import Trace, { ConsoleTraceObject } from "./utils/Trace"

import { createApp } from "vue"
import App from "./App.vue"
import router from "./utils/router"

import "./assets/css/theme"
import FontAwesomeIcon from "./assets/font-awesome"
import "./assets/tailwind.css"

import helloFreshAPI from "./API/HelloFreshAPI"

async function init() {
    Trace.setLogLevel(Trace.WARN)
    Trace.registerTraceObject(ConsoleTraceObject)

    helloFreshAPI.defaultHeaders["Accept"] = "application/json, text/plain, */*"
    helloFreshAPI.defaultHeaders["Content-Type"] = "application/json;charset=utf-8"

    try {
        const mockRules = "../external/MockData/MockRules"
        await require(mockRules).default()
        // eslint-disable-next-line no-empty
    } catch {}
}

async function setupApp() {
    await init()
    createApp(App)
        .use(router)
        .component("font-awesome-icon", FontAwesomeIcon)
        .mount("#app")
}

setupApp()
