import helloFreshAPI, { APIPaths } from "../HelloFreshAPI"

class ProductsAPI {
    /**
     * @param {Number} subscriptionId
     * @param {Date} date
     *
     * @returns {Promise<ProductListResponseModel>}
     */
    async requestItems(subscriptionId, sku, date) {
        const weekNumber = date.getWeekNumber() + ((date.getDay() || 7) > 2 ? 1 : 0)
        const weekIdentifier = `${date.getFullYear()}-W${weekNumber}`

        const response = await helloFreshAPI.sendRequest(APIPaths.PRODUCT_URL, { "product-sku": sku, locale: "nl-NL", preference: "veggie", servings: 1, subscription: subscriptionId, week: weekIdentifier }, {})

        if (response.hasError()) {
            return false
        }

        /** @type { ProductListResponseModel }*/
        const responseModel = response.getContent()
        return responseModel
    }
}

export const productsAPI = new ProductsAPI()
export default productsAPI
