<template>
    <div id="overview-container" ref="listContainer">
        <div v-for="(voteItem, index) in voteList" v-bind:key="voteItem.meal.recipe.id" class="list-item">
            <div class="meal-container">
                <meal-card :meal="voteItem.meal" />
            </div>
            <div id="vote-box">
                <div v-on:click="likeCard(index)" :class="'button bg-green-500' + (voteItem.score > 0 ? ' highlight' : '')"><font-awesome-icon icon="thumbs-up" /></div>
                <div v-on:click="dislikeCard(index)" :class="'button bg-red-700' + (voteItem.score < 0 ? ' highlight' : '')"><font-awesome-icon icon="thumbs-down" /></div>
            </div>
        </div>
    </div>
    <div class="my-5 bg-yellow-300 w-4/5 py-2 font-bold text-center cursor-pointer rounded select-none" v-on:click="clickDone">Done</div>
</template>

<script>
    import Trace from "../utils/Trace"
    import MealCard from "./MealCard.vue"

    const methods = {
        clickDone() {
            this.$emit("finished", this.voteList)
        },
        dislikeCard(index) {
            const item = this.voteList[index]
            item.score = item.score === -1 ? 0 : -1
        },
        likeCard(index) {
            const item = this.voteList[index]
            item.score = item.score === 1 ? 0 : 1
        }
    }

    export default {
        name: "CardsSelect",
        props: ["voteList"],
        emits: ["finished"],
        components: { MealCard },
        watch: {
            voteList() {
                this.$refs.listContainer.scrollTop = 0
            }
        },
        methods
    }
</script>

