import * as Router from "vue-router"
import Login from "../components/Login.vue"
import Home from "../components/Home.vue"
import helloFreshAPI from "../API/HelloFreshAPI"

const loginRoute = {
    name: "login",
    path: "/login",
    component: Login,
    meta: {
        requiresAuth: false
    }
}

const routes = [
    {
        name: "home",
        path: "/",
        component: Home,
        meta: {
            requiresAuth: true
        }
    },
    loginRoute
]

const router = Router.createRouter({
    history: Router.createWebHashHistory(),
    routes
})

/**
 * @param {Object} route
 * @param {String} paramName
 * @return {Object|undefined}
 */
const getMetaParam = (route, paramName) => {
    if (!Object.prototype.hasOwnProperty.call(route, "meta")) {
        return undefined
    }

    return route.meta[paramName]
}

router.beforeEach((requestedDestination, from, next) => {
    const requiresAuth = getMetaParam(requestedDestination, "requiresAuth") !== false

    if (requiresAuth && !helloFreshAPI.hasBearerToken()) {
        next(loginRoute)
        return
    }

    next()
})

export default router
