class MockServer {
    /**
     * @type {Array<MockRule>}
     */
    #rules = []

    constructor() {
        //
    }

    /**
     * @param {String} URL
     * @param {String} method
     * @returns {MockRule}
     */
    getRule(URL, method) {
        return this.#rules.find((rule) => rule.isValidFor(URL, method))
    }

    /**
     * @param {MockRule} rule
     */
    addRule(rule) {
        this.#rules.push(rule)
    }
}

export default MockServer
