<template>
    <div id="meal-box">
        <font-awesome-icon v-if="canClose" class="self-end cursor-pointer" icon="times" v-on:click="this.$emit('closed')" />
        <div id="title">
            <FitText :max="36" :min="12">{{ mealTitle }}</FitText>
        </div>
        <div id="image">
            <img :class="imageLoading ? 'filter blur-md' : ''" v-bind:src="mealImage" @load="imageLoading = false" />
        </div>
        <a v-bind:href="mealLink" target="_blank">Go to site</a>
    </div>
</template>

<script>
    import Trace from "../utils/Trace"
    import FitText from "./FitText.vue"

    const methods = {}

    export default {
        name: "MealCard",
        components: {
            FitText
        },
        props: ["meal", "canClose"],
        watch: {
            meal: {
                handler(newValue, oldValue) {
                    /** @type {{
                        meal: Meal
                    }} */
                    const { meal } = this

                    this.imageLoading = true

                    if (meal === null) {
                        this.mealTitle = "???"
                        this.mealImage = "#"
                        this.mealLink = "#"
                    } else {
                        this.mealTitle = meal.recipe.name
                        this.mealImage = meal.recipe.image
                        this.mealLink = meal.recipe.websiteURL
                    }
                },
                immediate: true
            }
        },
        methods,
        data: () => {
            return {
                mealTitle: "",
                mealImage: "",
                mealIngredients: "",
                imageLoading: false
            }
        }
    }
</script>

