<template>
    <form ref="form" id="form" class="bg-blue-300 shadow-md rounded px-8 pt-6 pb-8 flex flex-col w-screen text-white text-lg" method="POST" action="#" v-on:submit.prevent="addProduct">
        <div class="row">
            <h3 class="uppercase mb-10">Sign In</h3>
        </div>
        <div class="row">
            <label for="username">Username</label>
            <input type="email" name="username" required="required" class="rounded text-black pl-2 py-2" v-model="username" />
        </div>
        <div class="row">
            <label for="password">Password</label>
            <input type="password" name="password" required="required" class="rounded text-black pl-2 py-2" v-model="password" />
        </div>
        <div class="row">
            <input type="submit" value="Login" class="mt-10 bg-yellow-300 px-2 py-2 font-bold text-center cursor-pointer w-32 h-18 flex rounded self-end select-none" />
        </div>
    </form>
</template>

<script>
    import Trace from "../utils/Trace"
    import LoginAPI from "../API/login/LoginAPI"

    const methods = {
        async addProduct() {
            const response = await LoginAPI.login(this.username, this.password)
            if (response !== true) {
                // ERROR
                return
            }

            this.$router.push("/")
        }
    }
    export default {
        name: "App",
        methods,
        data: () => {
            return {
                text: "LOGIN",
                username: "",
                password: ""
            }
        }
    }
</script>

<style scoped>
    #form h3 {
        font-size: 36px;
        font-weight: bold;
    }

    .row {
        display: flex;
        flex-direction: column;
        margin: 10px;
    }
</style>
