Array.prototype.sum = function() {
    var total = 0
    const length = this.length

    for (var i = 0; i < length; i++) {
        total += this[i]
    }

    return total
}

Array.prototype.first = function() {
    return this[0]
}

Array.prototype.firstOrDefault = function(defaultValue) {
    if (this.length > 0) {
        return this[0]
    }

    return defaultValue
}

Array.prototype.last = function() {
    return this[this.length - 1]
}

Array.prototype.lastOrDefault = function(defaultValue) {
    if (this.length > 0) {
        return this[this.length - 1]
    }

    return defaultValue
}

Array.prototype.range = function() {
    var self = this.sort()
    return {
        min: self[0],
        max: self[this.length - 1]
    }
}

Array.prototype.groupBy = function(getKeyPredicate) {
    const result = {}
    const getGroup = (groupname) => {
        if (Object.prototype.hasOwnProperty.call(result, groupname)) {
            return result[groupname]
        }

        return (result[groupname] = [])
    }

    this.forEach((item) => {
        const key = getKeyPredicate(item)
        const group = getGroup(key)
        group.push(item)
    })

    return result
}

Array.prototype.shuffle = function() {
    return this.sort(() => 0.5 - Math.random())
}

Array.prototype.sample = function() {
    return this[Math.floor(Math.random() * this.length)]
}

Array.prototype.sampleSize = function(n) {
    const shuffled = this.shuffle()
    return shuffled.slice(0, n)
}
