import Trace from "../../utils/Trace"
import helloFreshAPI, { APIPaths } from "../HelloFreshAPI"
import loginRequestFactory from "./Factories/LoginRequestFactory"

export class LoginAPI {
    /**
     * @param {String} username
     * @param {String} password
     * @return {Promise<Boolean>}
     */
    async login(username, password) {
        const request = loginRequestFactory.createLoginRequest(username, password)
        Trace.logDebug(request)
        const response = await helloFreshAPI.sendRequest(APIPaths.LOGIN_URL, {}, { method: "POST", body: request, headers: { Authorization: null } })

        if (response.hasError()) {
            return false
        }

        /** @type { LoginResponseModel } */
        const responseModel = response.getContent()
        helloFreshAPI.setBearerToken(responseModel.access_token)

        Trace.logDebug("Token: ", responseModel.access_token)

        return true
    }
}

export const loginAPI = new LoginAPI()
export default loginAPI
