import Trace from "../utils/Trace"
import apiResponseFactory from "./core/Factories/APIResponseFactory"
import APIErrorResponse from "./core/Models/APIErrorResponse"
import APIFetchErrorResponse from "./core/Models/APIFetchErrorResponse"
import APIParseErrorResponse from "./core/Models/APIParseErrorResponse"
import APIResponse from "./core/Models/APIResponse"
import MockServer from "./mock/MockServer"

export class APIPaths {
    constructor() {
        throw new Error("Construction of static class")
    }

    static PROTOCOL = "https://"
    static HOSTNAME = "www.hellofresh.nl"
    static BASE_PATH = "/gw"

    static BASE_URL = this.PROTOCOL + this.HOSTNAME + this.BASE_PATH

    static LOGIN_URL = this.BASE_URL + "/login"
    static SUBSCRIPTION_URL = this.BASE_URL + "/api/customers/me/subscriptions"
    static PRODUCT_URL = this.BASE_URL + "/my-deliveries/menu"
}

class HelloFreshAPI {
    defaultHeaders = {}
    #bearerToken = null

    /** @type {MockServer} */
    mockServer

    constructor() {
        this.mockServer = new MockServer()
    }

    setBearerToken(token) {
        this.#bearerToken = token
    }

    removeBearerToken() {
        this.#bearerToken = null
    }

    hasBearerToken() {
        return this.#bearerToken !== null
    }

    createQuery(queryData) {
        const keys = Object.keys(queryData)
        if (keys.length === 0) {
            return ""
        }

        const queryParams = keys.map((paramKey) => {
            const paramValue = queryData[paramKey]
            return `${paramKey}=${paramValue}`
        })

        return "?" + queryParams.join("&")
    }

    #applyDefaultHeaders(currentHeaders) {
        const defaultHeaderNames = Object.keys(this.defaultHeaders)

        const headerExists = (name) => {
            return Object.prototype.hasOwnProperty.call(currentHeaders, name)
        }

        const applyHeader = (name, value) => {
            if (!headerExists(name)) {
                currentHeaders[name] = value
                return
            }

            if (currentHeaders[name] === null) delete currentHeaders[name]
        }

        defaultHeaderNames.forEach((headerName) => {
            const defaultHeaderValue = this.defaultHeaders[headerName]
            applyHeader(headerName, defaultHeaderValue)
        })

        applyHeader("Authorization", `Bearer ${this.#bearerToken}`)
    }

    /**
     * Send API Request
     *
     * @param {String} URL
     * @param {Object} queryData
     * @param {String} [method]
     * @param {String|Object} [body]
     * @param {Object} [headers]
     *
     * @return {Promise<import("./core/Models/APIResponse").APIResponse>}
     */
    async sendRequest(URL, queryData, { method = "GET", body = null, headers = {} }) {
        method = method.toUpperCase()

        const completeUrl = URL + this.createQuery(queryData)
        Trace.logDebug("Fetch ", completeUrl)

        /** @type {APIResponse} */
        var response

        const mockRule = this.mockServer.getRule(URL, method)
        if (mockRule !== undefined) {
            Trace.logDebug("Apply mock rule")
            response = mockRule.getResponse(queryData, body, headers)
        } else {
            if (typeof body === "object" && body !== null) {
                body = JSON.stringify(body)
            }

            this.#applyDefaultHeaders(headers)

            response = await apiResponseFactory.createFromRequest(completeUrl, {
                method,
                body,
                headers
            })
        }

        if (response instanceof APIFetchErrorResponse) {
            Trace.logError("Failed to fetch, " + response)
        } else if (response instanceof APIParseErrorResponse) {
            Trace.logError("Failed to parse, " + response)
        } else if (response instanceof APIErrorResponse) {
            Trace.logWarn("Repsonse failed, " + response)
        }

        return response
    }
}

const helloFreshAPI = new HelloFreshAPI()
export default helloFreshAPI
